/*
  This boilerplate is based in ITCSS and SMACSS
*/
:root {
  --color-font: $color-font;
  --size: 3rem;
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1, h2, h3, h4, p {
  margin: 0;
  margin-bottom: 1em;
}

h1:last-child, h1:last-of-type, h2:last-child, h2:last-of-type, h3:last-child, h3:last-of-type, h4:last-child, h4:last-of-type, p:last-child, p:last-of-type {
  margin-bottom: 0;
}

section, article, figure, picture {
  margin-bottom: 0;
}

body {
  margin: 0;
  font-family: "Trueno", "sans-serif";
  background: #010101;
  height: 100vh;
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: 15vh 1fr 11vh;
  grid-template-areas: "header  " " main " " footer";
  grid-gap: 0;
  color: #F1F2F2;
  width: 100%;
  position: relative;
  overflow-y: hidden;
  overflow-x: hidden;
}

@media screen and (min-width: 1024px) and (max-width: 1440px) {
  body {
    transform: scale(0.9);
  }
}

header {
  grid-area: header;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

header img {
  width: 60%;
  max-width: 40vh;
}

footer {
  grid-area: footer;
  text-align: center;
  text-transform: uppercase;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 0 !important;
}

section {
  grid-area: main;
  display: flex;
  justify-content: center;
  align-items: center;
}

body,
.container {
  transform-origin: center;
}

@media screen and (min-width: 1024px) and (max-width: 1440px) {
  .container {
    transform: scale(0.8);
  }
}

.languageContainer p {
  text-align: center;
  text-transform: uppercase;
  padding: 0 1em;
}

@media screen and (max-width: 1024px) {
  .languageContainer p {
    font-size: 4vw;
  }
}

.flags-container {
  width: 100%;
  max-width: 500px;
  margin: 10vh auto 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3em;
}

.flags-container img {
  cursor: pointer;
}

.flags-container a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  line-height: 3.5em;
  font-family: "Kohinoor Bangla", "sans-serif";
  font-size: 2em;
}

@media screen and (max-width: 1024px) {
  .flags-container {
    max-width: 40%;
    display: flex;
    flex-direction: column;
  }
  .flags-container img:first-child {
    margin-bottom: 1em;
  }
}

.ageContainer {
  text-align: center;
  padding: 0 1em;
}

.ageContainer h1 {
  font-size: 1.8em;
  text-transform: uppercase;
  margin-bottom: 1em;
  font-family: "Trueno-light", "sans-serif";
  opacity: 0.9;
}

@media screen and (max-width: 1024px) {
  .ageContainer h1 {
    font-size: 4vw;
  }
}

.ageContainer .subTitle,
.ageContainer .textFooter {
  font-size: 0.9em;
  width: 80%;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
  font-weight: normal !important;
  margin-bottom: 3rem;
  opacity: 0.75;
}

@media screen and (max-width: 1024px) {
  .ageContainer .subTitle,
  .ageContainer .textFooter {
    font-size: 4vw;
  }
}

.ageContainer .textFooter {
  max-width: 650px;
  opacity: 0.5;
  text-transform: uppercase;
  margin-bottom: 0;
}

.ageContainer form {
  --mb: 13.5vh;
  margin-bottom: calc(var(--mb) + 2em);
  position: relative;
}

@media screen and (min-width: 1440px) {
  .ageContainer form {
    --mb: 9.5vh;
  }
}

.ageContainer form::after {
  content: "";
  position: absolute;
  display: block;
  width: 80%;
  max-width: 750px;
  height: 2px;
  bottom: calc(var(--mb) * -1);
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.3);
}

.ageContainer .form-container {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.5em;
}

.ageContainer .input-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: auto;
  margin-right: 1rem;
}

.ageContainer .input-group:last-child {
  margin-right: 0;
}

.ageContainer .input-group.space-4 {
  width: calc(var(--size) * 4);
}

.ageContainer .input-group.space-4 .input-container::after {
  box-shadow: var(--size) 0 rgba(255, 255, 255, 0.3), calc(var(--size) * 2) 0 rgba(255, 255, 255, 0.3);
}

.ageContainer .input-group.space-2 {
  width: calc(var(--size) * 2);
}

.ageContainer .input-group .input-container {
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  height: auto;
  position: relative;
}

.ageContainer .input-group .input-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: var(--size);
  width: 1px;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
}

.ageContainer .input-group input {
  height: calc(var(--size) * 1.25);
  line-height: calc(var(--size) * 1.25);
  width: 100%;
  border: none;
  background: transparent;
  color: #fff;
  font-size: 1.5em;
  font-family: inherit;
  letter-spacing: 1.4em;
  padding-left: 0.9em;
}

.ageContainer .input-group input.day-input {
  padding-left: 0.9em;
  letter-spacing: 1.2em;
}

.ageContainer .input-group input.moth-input {
  padding-left: 0.9em;
  letter-spacing: 1.3em;
}

.ageContainer input,
.ageContainer label {
  opacity: 0.5;
}

.ageContainer label {
  text-transform: uppercase;
  font-size: 0.7em;
  margin-bottom: 1em;
  font-family: Arial;
  font-weight: 300;
}

input:focus,
button:focus {
  outline: none;
  box-shadow: none;
}

input:active,
button:active {
  outline: none;
  box-shadow: none;
}

button.btn {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.3);
  text-transform: uppercase;
  line-height: calc(var(--size) * 0.75);
  padding: 0 calc(var(--size) * 0.5);
  cursor: pointer;
}

.main-footer {
  opacity: 0.9;
  padding: 0 1em;
}

.main-footer p {
  margin-bottom: 0;
}

@media screen and (max-width: 1024px) {
  .main-footer p {
    font-size: 4vw;
    transform: translateY(-8vh);
  }
}

@media screen and (max-width: 1024px) {
  .ageContainer {
    transform-origin: center;
    transform: scale(0.65);
  }
}
